<template>
  <!-- <transition name="todo" appear=""> -->
    <li>
    <label>
      <input type="checkbox" :checked="todo.done" @change="handleCheck(todo.id)"/>
      <!-- ‰∏çÊé®ËçêÁõ¥Êé•‰øÆËøôÁßçÂÅöÊ≥ïÔºåÂõ†‰∏∫‰øÆÊîπ‰∫Üprops -->
      <!-- <input type="checkbox" v-model="todo.done"/> -->
      <span v-show="!todo.isEdit">{{todo.title}}</span>
      <input 
        type="text" 
        @blur="handleBlur(todo,$event)" 
        v-show="todo.isEdit" 
        :value="todo.title"
        @keyup.enter="handleBlur(todo,$event)"
        v-focus="focusStatus"
      >
    </label>
    <button @click="handleDelete(todo.id)" class="btn btn-danger">Âà†Èô§</button>
    <button v-show="!focusStatus" @click="handleEdit(todo,$event)" class="btn btn-edit">ÁºñËæë</button>
  </li>
  <!-- </transition> -->
</template>

<script>
import PubSub from 'pubsub-js'

export default {
  name:'Item',
  data(){
    return {
      focusStatus:false,
    }
  },
  // Â£∞ÊòéÊé•Êî∂todoÂØπË±° Ë°åÂèÇ ÂêçÂ≠óË∑üListÂ±ûÊÄßÂêç‰∏ÄÊ†∑
  props:['todo',],
  methods:{
    // ÂãæÈÄâ/ÂèñÊ∂àÂãæÈÄâ
    handleCheck(id){
      // ÈÄöÁü•AppÁªÑ‰ª∂Â∞ÜÂØπÂ∫îÁöÑtodoÂØπË±°ÁöÑdoneÂèñÂèç
      this.$bus.$emit('checkTodo',id)
    },
    // Âà†Èô§
    handleDelete(id){
      if(confirm('Á°ÆËÆ§Âà†Èô§Âêóüë©üèªÔ∏è')){
        // ÈÄöÁü•AppÂà†Èô§ÂØπÂ∫îÁöÑtodoÂØπË±°
        // this.$bus.$emit('deleteTodo',id)
        PubSub.publish('deleteTodo',id)
      }
    },
    // ÁºñËæë
    handleEdit(todo,$event){
      if(todo.hasOwnProperty('isEdit')){
        todo.isEdit = true
        console.log('Â∑≤ÁªèÊúâ‰∫Ü');
      }else{
        this.$set(todo,'isEdit',true)
        console.log('Ê≤°Êúâ');
      }
      // todo.isEdit = true
      this.focusStatus = !this.focusStatus
      // Â¶ÇÊûúË¶ÅÊâãÂä®Ëé∑ÂèñÁÑ¶ÁÇπÔºåÈúÄË¶ÅÂú®DOMÊõ¥Êñ∞ÂêéÂèäinput‰ª•ÂèäÊòæÁ§∫ÂêéÊâçËÉΩËé∑ÂèñÁÑ¶ÁÇπÔºåÁî®nextTickÂú®‰∏ã‰∏ÄÊ¨°DOMÊõ¥Êñ∞ÂêéÊâßË°åÂõûË∞É
      /* this.$nextTick(function(){
        this.$refs.inputTitle.focus()
      }) */
    },
    // Enter&Â§±ÂéªÁÑ¶ÁÇπÂõûË∞É ÊâßË°å‰øÆÊîπÈÄªËæë
    handleBlur(todo,$event){
      this.todo.isEdit = this.focusStatus = false
      console.log('updataTodo',todo.id,$event.target.value);
      if(!$event.target.value.trim()) return alert('ËæìÂÖ•‰∏çËÉΩ‰∏∫Á©∫ÔºÅ')
      this.$bus.$emit('updataTodo',todo.id,$event.target.value)
    }
  }
}
</script>

