import Vue from "vue";
// ÂºïÂÖ•App
import App from './App.vue'
// ÂÖ≥Èó≠VueÁöÑÁîüÊàêÊèêÁ§∫
Vue.config.productionTip = false

// Ê≥®ÂÜå‰∏Ä‰∏™ÂÖ®Â±ÄËá™ÂÆö‰πâÊåá‰ª§ `v-focus`
Vue.directive('focus', {
  // ÂΩìË¢´ÁªëÂÆöÁöÑÂÖÉÁ¥†ÊèíÂÖ•Âà∞ DOM ‰∏≠Êó∂‚Ä¶‚Ä¶
  update: function (el,{value}) {
    if(value){
      // ËÅöÁÑ¶ÂÖÉÁ¥†
      el.focus()
    }
  }
})
// ÂàõÂª∫vm
new Vue({
  el:'#app',
  render: h => h(App),
  beforeCreate(){
    Vue.prototype.$bus = this
  }
})